import React from 'react';
import {HashRouter as Router,NavLink} from 'react-router-dom';
import $ from 'jquery'
import '../../css/gd3.css';
import img9 from '../../img/tu-39.jpg'

import Rstore from '../../redux/Rstore'
import Action from '../../redux/Action'

import { Icon } from 'antd';
import { Button } from 'antd';
class Gd extends React.Component{
    constructor(props){
        super(props)
        this.state={
            list:[]
        }
    }
    tap1(){
        console.log('11')
        // this.props.history.push('/xdsj')
    }
    playAll(id){
        $.ajax({
            type: "get",
            url: "https://api.bzqll.com/music/netease/songList",
            data: {
                key:'579621905',
                id:id
            },
            dataType: "json",
            success: function (data) {
                // console.log(data.data.songs);
                Rstore.dispatch(Action.playAll(JSON.stringify(data.data.songs)))
            }
        });
    }
    render(){
        var _this=this
        return(
            <div>
                <div className='gd-wrap'>
                    <div className='gd-w'>
                        <div className='gd1'>
                            <p>全部</p>                           
                            <Button>选择分类  <Icon type="down" /></Button>                            
                        </div>
                            <ul className='gd2'>
                                {
                                    this.state.list.map(function(item,i){
                                        return(
                                            <li key={i}>
                                                <NavLink to={{pathname:'/list3',state:{id:item.id}}}>
                                                     <img src={item.coverImgUrl}/>
                                                </NavLink>
                                                <div>
                                                    <span><Icon type="customer-service" theme="filled" /></span>    
                                                    <span>25万</span>  
                                                    <span><Icon onClick={_this.playAll.bind(_this,item.id)} type="play-circle" /></span>  
                                                </div>
                                                <p>{item.title}</p>
                                                <p>{item.creator}</p>
                                            </li>
                                        )
                                    })
                                }                   
                            </ul>
                    </div>
                    
                </div>
                <div className='gd-footer'>
                    <img src={img9}/>
                </div>
                <div id='gd-top'>
                    <span><Icon type="up" /></span>
                    <p>TOP</p>
                </div>
            </div>
        )
    }
    componentWillMount(){
        $(function(){
            $("#gd-top").click(function(){
                $("html,body").stop().animate({"scrollTop":0},500);
            })
            $(window).scroll( function() { 
                var scroll=$(document).scrollTop();
                //var aCL=document.getElementsByClassName("cl")
                //var scrolltop=document.documentElement.scrollTop || document.body.scrollTop;
            
                if(scroll>600){
                    $("#gd-top").fadeIn();
                }else{
                    $("#gd-top").fadeOut();
                }
            }); 
        })



        var _this=this;
        $.ajax({
            type:'get',
            url:'https://api.bzqll.com/music/netease/hotSongList',//10
            async:false,
            dataType:'json',
            data:{key:579621905,limit:50},
            success:function(data){
                // console.log(data.data)
                _this.setState({list:data.data})
            }
        })
    }
   
    
}
export default Gd;