import React from 'react'
import $ from 'jquery'
import '../css/MyMusic.css'
import mylove from '../img/mylove.jpg'
import { Button } from 'antd';
import { Table} from 'antd';
import Rstore from '../redux/Rstore'
import Action from '../redux/Action';
import {Link} from 'react-router-dom'
import { Menu} from 'antd';

const SubMenu = Menu.SubMenu;

class MyMusic extends React.Component{
    rootSubmenuKeys = ['sub1', 'sub2', 'sub4'];
    constructor(props){
        super(props);
        this.state={
            love:{},
            song:Rstore.getState().shoucang,
            scgedan:Rstore.getState().shoucanggedan,
            openKeys: ['sub1'],
            tit:'我喜欢的音乐',
            pic:mylove,
            name:'老白家的小可爱',
            des:''
        }
        this.playAll=this.playAll.bind(this);
        this.changes=this.changes.bind(this);
    }

    onOpenChange = (openKeys) => {
        const latestOpenKey = openKeys.find(key => this.state.openKeys.indexOf(key) === -1);
        if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.setState({ openKeys });
        } else {
        this.setState({
            openKeys: latestOpenKey ? [latestOpenKey] : [],
        });
        }
    }

    changes(){
        var _this=this;
        this.setState({song:_this.state.song.concat(Rstore.getState().shoucang),scgedan:_this.state.scgedan.concat(Rstore.getState().shoucanggedan)})
    }
    play(record){
        // console.log(record,rowkey);
        Rstore.dispatch(Action.play(record))
    }
    playAll(){
        Rstore.dispatch(Action.playAll(JSON.stringify(this.state.song)))
    }
    render(){
        const columns = [{
            title: '',
            dataIndex: 'key',
            key: 'key'
          },
           {
            title: '歌曲标题',
            dataIndex: 'name',
            key: 'name',
          }, {
            title: '歌手',
            dataIndex: 'singer',
            key: 'singer',
          },{
            title: '时长',
            dataIndex: 'time1',
            key: 'time1',
          }];
        return(
            <div className='myLove'>
                <div className='myL_cen'>
                    <div className='g-sd3'>
                        <Menu
                            mode="inline"
                            openKeys={this.state.openKeys}
                            onOpenChange={this.onOpenChange}
                            style={{ width: 256 }}
                        >
                            <SubMenu key="sub1" title={<span>歌单</span>}>
                                <Menu.Item key="1"><Link to={{pathname:'/myMusic',state:{gdid:'141998290',i:'my'}}}>
                                    <img src={this.state.love.coverImgUrl}/>
                                    <h2><p>我喜欢的音乐</p><span>{this.state.love.songNum}首</span></h2>
                                </Link></Menu.Item>
                            </SubMenu>
                            <SubMenu key="sub2" title={<span>我收藏的歌单({this.state.scgedan.length})</span>}>
                                {
                                    this.state.scgedan.map(function(item,i){
                                        return(
                                            <Menu.Item key={i+1}><Link to={{pathname:'/myMusic',state:{gdid:item.id,i:i}}}>
                                                <img  src={item.coverImgUrl}/>
                                                <h2>
                                                    <p>{item.title}</p>
                                                    <span>{item.songNum}首</span>
                                                </h2>
                                            </Link></Menu.Item>
                                            
                                        )
                                    })
                                }
                            </SubMenu>
                        </Menu>
                        
                    </div>
                    <div className='g-mn3'>
                    <div className='j_flag'>
                        <div className='jj_flag'>
                            <img src={this.state.pic}/> 
                            <div>
                                <h1>{this.state.tit}</h1>
                                <p>{this.state.name}</p>
                                <pre>{this.state.des}</pre>
                                <Button type="primary" onClick={this.playAll}>播放</Button>
                                <Button type="primary" icon="download">下载</Button>
                            </div>
                        </div>
                        <h2>歌曲列表&nbsp;&nbsp;&nbsp;<span>{this.state.song.length}首歌</span></h2>
                    </div>    
                    <Table dataSource={this.state.song} columns={columns} 
                            onRow={(record,rowkey)=>{                
                                return{                    
                                    onClick : this.play.bind(this,record,rowkey)    
                                }           
                            }}
                    />
                    </div>
                </div> 
            </div>           
        )
    }
    componentWillMount(){
        var _this=this;
        $.ajax({
            type: "get",
            url: "https://api.bzqll.com/music/netease/userSongList?key=579621905&uid=115119971",
            dataType: "json",
            success: function (data) {
                _this.setState({love:data.data[0],scgedan:data.data.splice(1,data.data.length-1).concat(_this.state.scgedan)})
                // console.log(_this.state.love,_this.state.scgedan)
            }
        });
        // console.log(_this.props);
        $.ajax({
            type: "get",
            url: "https://api.bzqll.com/music/netease/songList",
            data: {
                key:'579621905',
                id:_this.props.location.state.gdid
            },
            dataType: "json",
            success: function (data) {
                var song=data.data.songs.concat(_this.state.song);
                // console.log(song);
                for(var i=0;i<song.length;i++){
                    song[i].key=i+1;
                    var m='',s='';
                    m=song[i].time/60<10?"0"+Math.floor(song[i].time/60):Math.floor(song[i].time/60);
                    s=song[i].time%60<10?"0"+Math.floor(song[i].time%60):Math.floor(song[i].time%60);
                    song[i].time1=''+m+':'+s;
                }  
                _this.setState({song:song})     
            }
        });
    }
    componentDidMount(){
        Rstore.subscribe(this.changes);
        // console.log(Rstore.getState().shoucang);
    }
    componentWillReceiveProps(a){
        var gdi=a.location.state.i;
        var _this=this;
        $.ajax({
            type: "get",
            url: "https://api.bzqll.com/music/netease/songList",
            data: {
                key:'579621905',
                id:a.location.state.gdid
            },
            dataType: "json",
            success: function (data) {
                var song=data.data.songs;
                // console.log(song);
                for(var i=0;i<song.length;i++){
                    song[i].key=i+1;
                    var m='',s='';
                    m=song[i].time/60<10?"0"+Math.floor(song[i].time/60):Math.floor(song[i].time/60);
                    s=song[i].time%60<10?"0"+Math.floor(song[i].time%60):Math.floor(song[i].time%60);
                    song[i].time1=''+m+':'+s;
                }  
                console.log(gdi)
                if(gdi=='my'){
                    _this.setState({song:song,tit:'我喜欢的音乐',pic:mylove,name:'老白家的小可爱',des:''})                         
                }else{
                    _this.setState({song:song,tit:_this.state.scgedan[gdi].title,pic:_this.state.scgedan[gdi].coverImgUrl,name:_this.state.scgedan[gdi].creator,des:_this.state.scgedan[gdi].description})     
                }
            }
        });
    }
}

export default MyMusic;