import React from 'react';
import {HashRouter as Router,NavLink} from 'react-router-dom';
import $ from 'jquery'
import '../../css/list3.css';
import img1 from '../../img/gd31.jpg'
import img2 from '../../img/lunbo32.jpg'
import img3 from '../../img/lunbo33.jpg'
import img4 from '../../img/lunbo34.jpg'

import img9 from '../../img/tu-39.jpg'
import { Icon } from 'antd';
import { Button} from 'antd';


import Rstore from '../../redux/Rstore';
import Action from '../../redux/Action';
class List3 extends React.Component{
    constructor(props){
        super(props)
        this.state={
            detail:[],
            detail1:[],
        }
    }
    playAll(){
        console.log('play')
        Rstore.dispatch(Action.playAll(JSON.stringify(this.state.detail1)));
    }
    play(id){
        $.ajax({
            type:'get',
            url:'https://api.bzqll.com/music/netease/song',
            data:{
                key:'579621905',
                id:id
            },
            dataType:'json',
            success:function(data){
                console.log(data.data);
                Rstore.dispatch(Action.play(data.data))
            }
        })
        Rstore.dispatch(Action.playAll(JSON.stringify(this.state.detail1)));
    }
    render(){
        var _this=this;
        return(
            <div>
                <div className='list-wrap'>
                    <div className='list-w'>
                        <div className='list-wl'>
                            <div className='list-1'>
                                <img src={this.state.detail.songListPic}/>
                                <div className='list-1r'>
                                    <p>{this.state.detail.songListName}</p>
                                    <div>
                                        <img src={this.state.detail.songListPic}/>
                                        <span>跨过凌晨<i className='iconfont icon-icon-test'></i></span>
                                        <span>2018-6-23 创建</span>                                    
                                    </div>
                                    <div>
                                        <Button onClick={_this.playAll.bind(_this)}><span className='iconfont icon-bofang'></span><span>播放</span></Button>
                                        <Button><span className='iconfont icon-icon_share'></span></Button>
                                        <Button><span className='iconfont icon-xiazai'></span></Button>
                                        <Button><span className='iconfont icon-xiaoxi1'></span></Button>
                                    </div>
                                    <div>
                                        <span>标签:</span>
                                        <Button>华语</Button>
                                        <Button>治愈</Button>
                                        <Button>流行</Button>
                                    </div>

                                </div>
                            </div>
                            
                            <div className='list-2'>
                                <p>歌曲列表</p>                           
                                                         
                            </div>
                            <div className='list-3'>
                                <span></span>
                                <span>标题</span>
                                <span>时长</span>
                                <span>歌手</span>
                            </div>
                            <ul className='list-4'>
                                {
                                    this.state.detail1.map(function(item,i){
                                        return(                                       
                                            <li key={i}>
                                                <span>{i+1}</span>
                                                <span>
                                                    <img src={item.pic}/>
                                                    <span className='iconfont icon-bofang' onClick={_this.play.bind(_this,item.id)}></span>
                                                    <NavLink to={{pathname:'/geci',state:{id:item.id}}}>
                                                        <span>{item.name}</span>
                                                    </NavLink>
                                                </span>
                                                <span>{'0'+Math.floor(item.time/60)+':'+item.time%60}</span>
                                                <span>{item.singer}</span>
                                            </li>
                                         )
                                    })
                                }
                                    
                            </ul>
                        </div>
                        <div className='list-wr'>
                            <div className='list-wr1'>
                                喜欢这首歌的人
                            </div>
                            <ul className='list-wr2'>
                                <li><img src={img1}/></li>
                                <li><img src={img2}/></li>
                                <li><img src={img3}/></li>
                                <li><img src={img4}/></li>
                            </ul>
                            <div className='list-wr3'>
                                 包含这首歌的歌单
                            </div>
                            <ul className='list-wr4'>
                                <li>
                                    <div><img src={img4}/></div>
                                    <div>
                                        <p>初恋糖配料表：你的马尾辫和衬衫味道</p>
                                        <p>车厘子焯水<i className='iconfongt icon-icon-test'></i></p>
                                    </div>
                                </li>
                                <li>
                                    <div><img src={img2}/></div>
                                    <div>
                                        <p>初恋糖配料表：你的马尾辫和衬衫味道</p>
                                        <p>车厘子焯水<i className='iconfongt icon-icon-test'></i></p>
                                    </div>
                                </li>
                                <li>
                                    <div><img src={img1}/></div>
                                    <div>
                                        <p>初恋糖配料表：你的马尾辫和衬衫味道</p>
                                        <p>车厘子焯水<i className='iconfongt icon-icon-test'></i></p>
                                    </div>
                                </li>
                                <li>
                                    <div><img src={img3}/></div>
                                    <div>
                                        <p>初恋糖配料表：你的马尾辫和衬衫味道</p>
                                        <p>车厘子焯水<i className='iconfongt icon-icon-test'></i></p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div className='gd-footer'>
                    <img src={img9}/>
                </div>
                <div id='gd-top'>
                    <span><Icon type="up" /></span>
                    <p>TOP</p>
                </div>
            </div>
        )
    }

    componentWillMount(){
        $(function(){
            $("#gd-top").click(function(){
                $("html,body").stop().animate({"scrollTop":0},500);
                // console.log('ddd')
            })
            $(window).scroll( function() { 
                var scroll=$(document).scrollTop();
                //var aCL=document.getElementsByClassName("cl")
                //var scrolltop=document.documentElement.scrollTop || document.body.scrollTop;
            
                if(scroll>600){
                    $("#gd-top").fadeIn();
                }else{
                    $("#gd-top").fadeOut();
                }
            }); 
        })

        // console.log(this.props.location.state.id)
        var _this=this
        $.ajax({
            type:'get',
            url:'https://api.bzqll.com/music/netease/songList',//1
            async:false,
            dataType:'json',
            data:{key:579621905,id:_this.props.location.state.id,limit:50},
            success:function(data){
                 console.log(data.data)
                // console.log(data.data.songs)
                _this.setState({detail:data.data})
                _this.setState({detail1:data.data.songs})
                // console.log(_this.state.detail,_this.state.detail1)
            }
        })
    }
   
    
}
export default List3;