import React, { Component } from 'react';
// import {BrowserRouter as Router,Route,Link,NavLink,Redirect,Switch} from 'react-router-dom';
import logo from './img/topbar.png';
import './App.css';
import { Icon } from 'antd';
import $ from 'jquery';

import Wxlogin from './components/Wxlogin';
import Login1 from './components/Login1';
import Login2 from './components/Login2';
import Register from './components/Register';
import Qqlogin from './components/Qqlogin';
import Wblogin from './components/Wblogin';
import Mine from './components/Mine';


import {HashRouter as Router,Route,Link,NavLink,Redirect,Switch} from 'react-router-dom'

import Fxyy from './components/Fxyy';

// import FindMusic from './components/FindMusic'
import MyMusic from './components/MyMusic'


import touxiang from './img/toxiang.jpg'
import toplist from './img/toplist.png'
import Player from './components/Player'
import Rstore from './redux/Rstore';
var bg={
  backgroundImage:`url(${logo})`,
  backgroundPosition:"0 -98px",
  backgroundRepeat: 'no-repeat'
}
var bg1={
  backgroundImage:`url(${toplist})`,
  backgroundPosition:"-20px -2px",
  backgroundRepeat: 'no-repeat'
}
class App extends Component {   
  constructor(props){
    super(props);
    this.state={
      login:'',
      my:'none'
    }
    this.changes=this.changes.bind(this);
  }
  
  tui(){

  }
  render() {
    
    return (
      <div className="App">   
        <div className='header'>
        <Router>
          <div>
          <Player/>
          <div className='headCen'>
            <h1><img src={logo}/></h1>
            <ul className='nav'>
              <li><NavLink to='/fxyy'>发现音乐</NavLink></li>
              <li><NavLink to={{pathname:'/myMusic',state:{gdid:'141998290'}}}>我的音乐</NavLink></li>
              <li>朋友</li>
              <li>商城</li>
              <li>音乐人</li>
              <li>下载客户端</li>
            </ul>
            <h4><Link to='/login1'>{this.state.login}</Link></h4>
            <div className='search'>
              <input style={bg} type='text' placeholder='音乐/视频/电台/用户' />
              <div className='my'>
                <img src={touxiang}/>                 
                <ul style={{display:this.state.my}}>
                  <li className='arr'>
                    <i style={bg1}></i>
                  </li>
                  <li>
                  <Icon type="user" />
                    <em>我的主页</em>
                  </li>
                  <li>
                  <Icon type="mail" />
                    <em>我的消息</em>
                  </li>
                  <li>
                  <Icon type="crown" />
                    <em>VIP会员</em>
                  </li>
                  <li>
                  <Icon type="setting" />
                    <NavLink to="/mine"><em>个人设置</em></NavLink>
                  </li>
                  <li>
                  <Icon type="safety" />
                    <em>实名认证</em>
                  </li>
                  <li>
                  <Icon type="poweroff" />
                    <em onClick={this.tui.bind(this)}>退出</em>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <Switch>
              <Route path='/fxyy' component={Fxyy}></Route>
              <Route path='/myMusic' component={MyMusic}></Route> 

              <Route path='/login1' component={Login1}></Route>
              <Route path='/login2' component={Login2}></Route>
              <Route path='/wxlogin' component={Wxlogin}></Route>
              <Route path='/register' component={Register}></Route>
              <Route path='/qqlogin' component={Qqlogin}></Route>
              <Route path='/wblogin' component={Wblogin}></Route>
              <Route path='/mine' component={Mine}></Route>
              
              <Redirect to='/fxyy'></Redirect>
            </Switch>
            </div>
          </Router>
        </div>
      
      </div>
    );
  }
  componentWillMount(){
    this.setState({login:'登录'});
  }
  changes(){
    this.setState({login:Rstore.getState().users})
  }
  componentDidMount(){
    Rstore.subscribe(this.changes);
    var _this=this;
    $('.my img,.my ul').mouseover(function(){
      _this.setState({my:'block'})
    })
    $('.my img,.my ul').mouseout(function(){
      _this.setState({my:'none'})
    })
  }
}

export default App;
