import React from 'react';
import img from '../img/J`%]6ZM]%X%O)LHW_LK8CO5.png';
import '../css/Login1.css';
import Button from 'antd/lib/button';
import { Icon } from 'antd';


class Login1 extends React.Component{
    constructor(props){
        super(props)
        this.state={
            display:'block'
        }
    }
   
    tap(){
        this.props.history.push('/login2')
    }
    tap1(){
        this.setState({display:'none'})
        this.props.history.push('/tuijian')
    }
    tap2(){
        this.props.history.push('/wxlogin')
    }
    tap3(){
        this.props.history.push('/qqlogin')
    }
    tap4(){
        this.props.history.push('/wblogin')
    }
    tap5(){
        this.props.history.push('/register')
    }
    render(){
      return(
        <div className='log1' style={{display:this.state.display}}>
            <h2>登录</h2>
            <em onClick={this.tap1.bind(this)}>X</em>
            <div className="left">
                <img src={img}/>
                <Button type="primary" block onClick={this.tap.bind(this)}>账号登录</Button>
                <Button type="danger" block onClick={this.tap5.bind(this)}>注册</Button>
            </div>
            <ul className="right">
                <li  onClick={this.tap2.bind(this)}>
                    <Icon type="wechat" />
                    <span>微信登录</span>
                </li>
                <li  onClick={this.tap3.bind(this)}>
                    <Icon type="qq" />
                    <span>QQ登录</span>
                </li>
                <li  onClick={this.tap4.bind(this)}>
                    <Icon type="weibo" />
                    <span>微博登录</span>
                </li>
            </ul>
            
           

        </div>
      )
    }
  }
  export default Login1;