var Player=(state=[],action)=>{
    switch(action.type){
        case 'PLAYALL':
            let newstate=JSON.parse(action.text);
            return newstate;
        case 'PLAY':
            state=[];
            let newstates=[...state];
            newstates.push(action.text);
            return newstates;
        case 'ADDLIST':
            let newstate2=[...state];
            newstate2.push(action.text);
            return newstate2;
        default:
            return state;    
    }
}
export default Player;