import React from 'react';
import img from '../img/QQ图片20190121145319.png';
import '../css/Wxlogin.css';
class Wxlogin extends React.Component{
    constructor(props){
        super(props)
        this.state={
            display:'block'
        }
    }
    tap1(){
        this.setState({display:'none'})
    }
    render(){
        return(
            <div id="wx" style={{display:this.state.display}}>
                <h2>微信登录</h2>
                <em onClick={this.tap1.bind(this)}>X</em>
                <img src={img}/>
                {/* <img src={require("../img/QQ图片20190121145319.png")} alt=""/> */}
                <p>请使用微信扫描二维码登录 "网易云音乐"</p>
            </div>
        )
    }
}
export default Wxlogin;