import React from 'react';
import img from '../img/OJU~8`JR{JDR83ZW1G4L3(J.png';
import '../css/Qqlogin.css';
class Wxlogin extends React.Component{
    constructor(props){
        super(props)
        this.state={
            display:'block'
        }
    }
    tap1(){
        this.setState({display:'none'})
        this.props.history.push('/app')
        
    }
    render(){
        return(
            <div className="qq" style={{display:this.state.display}}>
                <h3>请使用QQ手机版扫描二维码登录</h3>
                <em onClick={this.tap1.bind(this)}>X</em>
                <img src={img}/>  
            </div>
        )
    }
}
export default Wxlogin;