import React from 'react';
import $ from 'jquery'
import '../../css/xdsj3.css';
import img9 from '../../img/tu-39.jpg';
import {NavLink} from 'react-dom';
import { Icon } from 'antd';

import Rstore from '../../redux/Rstore'
import Action from '../../redux/Action'
class Xdsj extends React.Component{
    constructor(props){
        super(props)
        this.state={
            list:[]
        }
    }
    tap1(){

    }
    play(id){
        $.ajax({
            type: "get",
            url: "https://api.bzqll.com/music/netease/song",
            data: {
                key:'579621905',
                id:id
            },
            dataType: "json",
            success: function (data) {
                // console.log(data.data);
                Rstore.dispatch(Action.play(data.data))
            }
        });
    }
    render(){
        var _this=this
        return(
            <div>
                   <div className='gd-wrap'>
                    <div className='gd-w'>
                        <div className='gd1'>
                            <p>热门新碟</p>                           
                        </div>
                            <ul className='gd2'>
                                {
                                    this.state.list.map(function(item,i){
                                        return(
                                            <li key={i}>
                                                <NavLink to={{pathname:'/geci',state:{id:item.id}}}>
                                                    <img src={item.pic}/>
                                                </NavLink>
                                                <div>
                                                    <span><Icon type="customer-service" theme="filled" /></span>    
                                                    <span>25万</span>  
                                                    <span><Icon onClick={_this.play.bind(_this,item.id)} type="play-circle" /></span>  
                                                </div>
                                                <p>{item.name}</p>
                                                <p>{item.singer}</p>
                                            </li>
                                        )
                                    })
                                }                   
                            </ul>
                    </div>
                    
                    </div>
                    <div className='gd-footer'>
                        <img src={img9}/>
                    </div>
                    <div id='gd-top'>
                        <span><Icon type="up" /></span>
                        <p>TOP</p>
                    </div>
            </div>
        )
    }
    componentWillMount(){
        $(function(){
            $("#gd-top").click(function(){
                $("html,body").stop().animate({"scrollTop":0},500);
                console.log('ddd')
            })
            $(window).scroll( function() { 
                var scroll=$(document).scrollTop();
                //var aCL=document.getElementsByClassName("cl")
                //var scrolltop=document.documentElement.scrollTop || document.body.scrollTop;
            
                if(scroll>600){
                    $("#gd-top").fadeIn();
                }else{
                    $("#gd-top").fadeOut();
                }
            }); 
        })



        var _this=this;
        $.ajax({
            type:'get',
            url:'https://api.bzqll.com/music/netease/songList',//1
            async:false,
            dataType:'json',
            data:{key:579621905,limit:10,id:3778678},
            success:function(data){
                console.log(data.data.songs)
                _this.setState({list:data.data.songs})
            }
        })


    }
    
}
export default Xdsj;