import React from 'react';
// import img from '../img/QQ图片20190121145319.png';
import '../css/mine.css';
import Button from 'antd/lib/button';
import Rstore from '../redux/Rstore'; 
import img from '../img/gd31.jpg';
import Action from '../redux/Action'
class Mine extends React.Component{
    constructor(props){
        super(props)
        this.state={
            user:Rstore.getState().users,
            num:10,
            sum:99
        }
    }
    tap(){
        Rstore.dispatch(Action.login(this.refs.ipt.value));
        this.props.history.push('/tuijian');
    }
    componentDidMount(){
        console.log(Rstore.getState().users)
    }
    render(){
        return(
            <div id="mine">
                <h1>Imusic个人设置</h1>
                <h4>基本设置</h4>
                <div>
                    <h5>我的头像:</h5>
                    <img src={img}/>
                </div>
                <div id='auser'>
                    用户名: <input type='text' ref='ipt' placeholder={this.state.user}/><br/>
                    <i>关注 : {this.state.num}</i>
                    <i>粉丝 : {this.state.sum}</i>
                </div>
                <Button type="primary" onClick={this.tap.bind(this)}>保存</Button>
                    
               
                
            </div>
        )
    }
}
export default Mine;