import React from 'react';
import Button from 'antd/lib/button';
import '../css/Login2.css';
import $ from 'jquery'
import Rstore from '../redux/Rstore'; 
import Action from '../redux/Action';

  class Login2 extends React.Component {
    constructor(props){
      super(props)
        this.state={
          display:'block',
          user:Rstore.getState().users
      }
      this.changes=this.changes.bind(this)
    }
    tap1(){
      this.setState({display:'none'})
    }
    changes(){
      this.setState({user:Rstore.getState().users})
    }
    componentDidMount(){
      Rstore.subscribe(this.changes)
    }
    tap2(){  
      var _this=this;
      $.ajax({
        type:'get',
           url:'http://jx.xuzhixiang.top/ap/api/login.php',
           async:false,
           dataType:'json',
           data:{
             username:_this.refs.ipt2.value,
             password:_this.refs.ipt3.value
            },
           success:function(data){
              if(data.code==0){
                alert('输入有误,请重新输入!')
              }else{
                alert('登陆成功');
                Rstore.dispatch(Action.login(_this.refs.ipt2.value))
                // console.log(Rstore.getState().users)
              }
           }  
      })
      
       this.props.history.push('/fxyy')
    }
    render(){
      return(
        <div className="log2" style={{display:this.state.display}}>
          <h2>账号登录</h2>
          <i onClick={this.tap1.bind(this)}>X</i>
          <div className="li2">
            用户名 : <input type="text" ref='ipt2' placeholder=" 请输入账号"/><br/>
            密码 : <input type="passwold" ref='ipt3' placeholder=" 请输入密码"/>
            <input type="checkbox" ref="ipt4"/> :记住密码
            <Button type="primary" block onClick={this.tap2.bind(this)}>登录</Button>
          </div>
          <div className='hi'>
            <a>{this.state.user}</a>
            <a href='/login1'>其他方式登录</a>
            <a href='/register'>？立即注册</a>
            
          </div>
        
        </div>
      )
    }
  
      
 
  }
  export default Login2;