import React from 'react';
import Button from 'antd/lib/button';
import '../css/Register.css';
import $ from 'jquery';  
  class Register extends React.Component {
    constructor(props){
      super(props)
      this.state={
        display:'block',
        list:{}
    }
  }
  tap1(){
    this.setState({display:'none'})
  }
  tap2(){
    var _this=this;
    $.ajax({
      type:'get',
         url:'http://jx.xuzhixiang.top/ap/api/reg.php',
         async:false,
         dataType:'json',
         data:{
           username:_this.refs.ipt1.value,
           password:_this.refs.ipt1.value
          },
         success:function(data){
            console.log(data)
         }  
    })
    this.props.history.push('/login2')
  }
    render(){
      return(
        <div className="regis" style={{display:this.state.display}}>
          <h2>账号注册</h2>
          <i onClick={this.tap1.bind(this)}>X</i>
          <div className="lg2">
            用户名 : <input type="text" ref='ipt1' className='ipt1' placeholder=" 请输入账号"/><br/>
            密码 : <input type="passwold"  ref='ipt2' className='ipt2' placeholder=" 请输入密码"/>
            <Button type="primary" block onClick={this.tap2.bind(this)}>注册</Button>
          </div>
          <div className='foot'>
            <a href='/login2'>返回登录</a>
          </div>
        </div>
      )
    }
    componentWillMount(){
      
      
      
      
    }
  
}
  export default Register;