import React from 'react';
import img from '../img/SKJ2`(_REF_3ULV@FG1A2~W.png';
import '../css/Wblogin.css';
class Wblogin extends React.Component{
    constructor(props){
        super(props)
        this.state={
            display:'block'
        }
    }
    tap1(){
        this.setState({display:'none'})
    }
    render(){
        return(
            <div className="wb" style={{display:this.state.display}}>
                <em onClick={this.tap1.bind(this)}>X</em>
                <img src={img}/>  
            </div>
        )
    }
}
export default Wblogin;