import React from 'react'
import '../../css/Geci.css';
import $ from 'jquery'
import { Button} from 'antd';
import Rstore from '../../redux/Rstore';
import Action from '../../redux/Action';
const parseLyric = (lyricStr) => {
    // 将文本分隔成一行一行，存入数组
    let lines = lyricStr.split('\n')
    // 用于匹配时间的正则表达式，匹配的结果类似[xx:xx.xx]
    const pattern = /\[\d{2}:\d{2}.\d{1,3}\]/g
    // 保存最终结果的数组
    const result = []
    // 去掉不含时间的行
    while (!pattern.test(lines[0])) {
      lines = lines.slice(1)
    }
    // 上面用'\n'生成生成数组时，结果中最后一个为空元素，这里将去掉
    if (!lines[lines.length - 1].length) { lines.pop() }
    for (let i = 0; i < lines.length; i += 1) {
      const line = lines[i]
      const value = line.replace(pattern, '') // 提取歌词 将时间清空 返回一个新的字符串
      result.push(value);
      result.push('<br/>')

    }
    return result
  }
class Geci extends React.Component{ 
    constructor(props){
        super(props);
        this.state={
            music:{},
            h:'300px',
            text:'展开'
        }
    }
    zhankai(){
        if(this.state.h=='300px'){
            this.setState({h:'auto',text:'收起'})
        }else{
            this.setState({h:'300px',text:'展开'})
        }
        
    }
    playAll(){
        console.log('play')
        Rstore.dispatch(Action.playAll(JSON.stringify(this.state.music)));
    }
    render(){
        var _this=this;
        return(
            <div className="container">
                <div className='info'>
                    <img src={this.state.music.pic}/>
                    
                </div>
                <div className='list-1r'>
                    <p>{this.state.music.name}</p>
                    <div>
                        <img src={this.state.music.pic}/>
                        <span>歌手：</span>
                        <span>{this.state.music.singer}</span>                                    
                    </div>
                    <div>
                        <Button onClick={_this.playAll.bind(_this)}><span className='iconfont icon-bofang'></span><span>播放</span></Button>
                        <Button><span className='iconfont icon-icon_share'></span></Button>
                        <Button><span className='iconfont icon-xiazai'></span></Button>
                        <Button><span className='iconfont icon-xiaoxi1'></span></Button>
                    </div>
                    <div>
                        <span>标签:</span>
                        <Button>华语</Button>
                        <Button>治愈</Button>
                        <Button>流行</Button>
                    </div>

                </div>
                <div id="player3" className="aplayerG">
                    <p>{this.state.music.name}--{this.state.music.singer}</p>
                    <pre className="aplayerG-lrc-content" style={{height:this.state.h}}></pre>
                    <span className='zhankai' onClick={this.zhankai.bind(this)}>{this.state.text}</span>
                </div>
            </div>
            
        )
    }
    componentDidMount(){
        console.log(this.props)
        // console.log(this.props.location.state.id)
        var _this=this;
        $.ajax({
            type:"get",
            url:"https://api.bzqll.com/music/netease/song",
            data:{
                key:'579621905',
                id:_this.props.location.state.id
            },
            dataType:'json',
            success:function(data){
                // console.log(data.data);
                _this.setState({music:data.data})
                $.ajax({
                    type:"get",
                    url:data.data.lrc,
                    dataType:'text',
                    success:function(data){
                        console.log(parseLyric(data).join(''));
                        $('.aplayerG-lrc-content').html(parseLyric(data).join(''))
                        
                    }
                });
            }
        })   
    }
    componentWillReceiveProps(a){
              
    }
}
export default Geci;