import React from 'react'
import '../css/Player.css'
import $ from 'jquery'
import Rstore from '../redux/Rstore'
import APlayer from 'aplayer'
import '../css/player1.css'


class Player extends React.Component{
    constructor(props){
        super(props);
        this.state={
            tracks:[]
        }
        this.changes=this.changes.bind(this)
    }
    render(){
        // console.log(this.state.tracks)
        return(
            <div id='player'>

            </div>
        )
    }
    //歌单改变
    changes(){
        var _this=this;
        this.setState({tracks:Rstore.getState().tracks})
        
    }      
    componentDidMount(){
        Rstore.subscribe(this.changes);
        var _this=this;
        $.ajax({
            type: "get",
            url: "https://api.bzqll.com/music/netease/songList",
            data: {
                key:'579621905',
                id:'3778678',
                limit:10
            },
            dataType: "json",
            success: function (data) {
                _this.setState({tracks:data.data.songs.splice(0,10)}) 
                const ap = new APlayer({
                    container: document.getElementById('player'),
                    fixed: true,
                    autoplay: false,
                    theme: '#FADFA3',
                    loop: 'all',
                    order: 'random',
                    preload: 'auto',
                    volume: 0.7,
                    mutex: true,
                    listFolded: false,
                    listMaxHeight: '90px',
                    lrcType: 3,
                    audio: _this.state.tracks
                })
            }
        })
       
    }
    componentDidUpdate(){
        var _this=this;
        const ap = new APlayer({
            container: document.getElementById('player'),
            mini:false,
            listFolded: false,
            listMaxHeight: 90,
            lrcType: 0,
            theme: '#e9e9e9',
            autoplay: false,
            listFolded:false,
            audio: _this.state.tracks
        })
        ap.list.hide();
    }
}
export default Player;
