import React from 'react';
import {HashRouter as Router} from 'react-router-dom';
import $ from 'jquery'
import '../../css/mv3.css';
import img9 from '../../img/tu-39.jpg'

import { Icon } from 'antd';
class Mv extends React.Component{
    constructor(props){
        super(props)
        this.state={
            list:[]
        }
    }
    tap1(){
        console.log('dfff')
    }
    render(){
        var _this=this
        return(
            <div>
                   <div className='gd-wrap'>
                    <div className='gd-w'>
                        <div className='gd1'>
                            <p>最新MV</p>                           
                        </div>
                        <Router>
                            <div className='gd2'>
                                {
                                    this.state.list.splice(20,30).map(function(item,i){
                                        return(
                                            <li key={i} onClick={_this.tap1.bind(_this)}>
                                                
                                                <video src={item.url} controls='controls' poster={item.pic}></video>
                                                <div>
                                                    <span><Icon type="customer-service" theme="filled" /></span>    
                                                    <span>25万</span>  
                                                    <span><Icon type="play-circle" /></span>  
                                                </div>
                                                <p>{item.name}</p>
                                                <p>{item.singer}</p>
                                            </li>
                                        )
                                    })
                                }                   
                            </div>
                        </Router>
                        {/* <ul className='gd2'>
                            <li>
                                <img src={img1}/>
                                <div>
                                    <span><Icon type="customer-service" theme="filled" /></span>    
                                    <span>25万</span>  
                                    <span><Icon type="play-circle" /></span>  
                                </div>
                                <p>你是我拿时间和新欢都忘不掉的人</p>
                                <p>DJ顾念晨</p>
                            </li>                                                    
                        </ul> */}
                    </div>
                    
                    </div>
                    <div className='gd-footer'>
                        <img src={img9}/>
                    </div>
                    <div id='gd-top'>
                        <span><Icon type="up" /></span>
                        <p>TOP</p>
                    </div>
            </div>
        )
    }
    componentWillMount(){
        $(function(){
            $("#gd-top").click(function(){
                $("html,body").stop().animate({"scrollTop":0},500);
                console.log('ddd')
            })
            $(window).scroll( function() { 
                var scroll=$(document).scrollTop();
                //var aCL=document.getElementsByClassName("cl")
                //var scrolltop=document.documentElement.scrollTop || document.body.scrollTop;
            
                if(scroll>600){
                    $("#gd-top").fadeIn();
                }else{
                    $("#gd-top").fadeOut();
                }
            }); 
        })



        // var _this=this;
        // $.ajax({
        //     type:'get',
        //     url:'https://api.bzqll.com/music/netease/songList',//1
        //     async:false,
        //     dataType:'json',
        //     data:{key:579621905,limit:10,id:3778678},
        //     success:function(data){
        //         console.log(data.data.songs)
        //         _this.setState({list:data.data.songs})
        //     }
        // })


        var _this=this;
        $.ajax({
            type:'get',
            url:'https://api.bzqll.com/music/netease/topMvList',//11
            async:false,
            dataType:'json',
            data:{key:579621905,limit:50},
            success:function(data){
                console.log(data.data)
                 _this.setState({list:data.data})
            }
        })

    }
    
}
export default Mv;