import ShoucangGedan from "./ShoucangGedan";

var Action={
    playAll(text){
        return{
            type:'PLAYALL',
            text:text
        }
    },
    play(text){
        return{
            type:'PLAY',
            text:text
        }
    },
    addList(text){
        return{
            type:'ADDLIST',
            text:text
        }
    },
    shoucang(text){
        return{
            type:'SHOUCANG',
            text:text
        }
    },
    shoucanggedan(text){
        return{
            type:'SHOUCANGGEDAN',
            text:text
        }
    },
    login(text){
        return{
            type:'LOGIN',
            text:text
        }
    }
}
export default Action;