import React from 'react';
import {HashRouter as Router,Route,NavLink,Redirect,Switch} from 'react-router-dom';
import '../css/fxyy3.css';

import Tuijian from './fxyy/Tuijian';
import Tip from './fxyy/Tip';
import Gd from './fxyy/Gd';
import Mv from './fxyy/Mv';
import Xdsj from './fxyy/Xdsj';
import List3 from './fxyy/List3';
// import Gc3 from './fxyy/Gc3';
import Geci from './fxyy/Geci';
class Fxyy extends React.Component{
    render(){
        return(
            <div>             
               <Router>
                    <div>
                        <div className='fxyy1'>
                            <div>
                                <NavLink to='/tuijian'>推荐</NavLink>
                                <NavLink to={{pathname:'/tip',state:{id:'3778678'}}}>排行榜</NavLink>
                                <NavLink to='/gd'>歌单</NavLink>
                                <NavLink to='/mv'>MV</NavLink>
                                <NavLink to='/xdsj'>新碟上架</NavLink>
                            </div>
                        </div>

                        <Switch>
                            <Route path='/tuijian' component={Tuijian}></Route> 
                            <Route path='/tip' component={Tip}></Route>
                            <Route path='/gd' component={Gd}></Route>  
                            <Route path='/mv' component={Mv}></Route> 
                            <Route path='/xdsj' component={Xdsj}></Route> 
                            <Route path='/list3' component={List3}></Route>   
                            {/* <Route path='/gc3' component={List3}></Route>             */}
                            <Route path='/geci' component={Geci}></Route>
                            <Redirect to='/tuijian'/>
                        </Switch>
                    </div>
                </Router>
            </div>
        )
    }
}
export default Fxyy;



