import React from 'react';
import '../../css/tip.css';
import $ from 'jquery';
import {NavLink} from 'react-router-dom';
import { Icon } from 'antd';
import Rstore from '../../redux/Rstore';
import Action from '../../redux/Action';
class Tip extends React.Component{
    constructor(props){
        super(props)
        this.state={
            list:[],
            list2:[]
        }
    }
    play(id){
        $.ajax({
            type:'get',
            url:'https://api.bzqll.com/music/netease/song',
            data:{
                key:'579621905',
                id:id
            },
            dataType:'json',
            success:function(data){
                // console.log(data.data);
                Rstore.dispatch(Action.play(data.data))
            }
        })
        Rstore.dispatch(Action.playAll(JSON.stringify(this.state.list2)));
    }
    render(){
        var _this=this;
        return(
            <div id='tip'>
                <div className="lef">
                    <h2>MV排行榜</h2>
                    <hr/>
                    {
                       this.state.list.splice(0,10).map(function(item,i){
                           return(
                                <dl key={i} className="pic">
                                    <dt><video src={item.url} style={{width:'120px',height:'120px'}} controls="controls" poster={item.pic}></video></dt>
                                    <dd>{item.name}</dd>   
                                </dl>
                           )
                        }) 
                    }
                </div>
                <div className='rig'>
                    <h2>歌曲排行榜</h2>   
                    <ul>
                        <li>排名</li>
                        <li>标题</li>
                        <li>时长</li>
                        <li>歌手</li>
                    </ul>
                    {
                       this.state.list2.map(function(item,i){
                           return(
                               <div key={i} className='lis'>
                                    <span>{i+1}</span>
                                    <NavLink to={{pathname:'/geci',state:{id:item.id}}}>
                                        <span>{item.name}</span>
                                    </NavLink>
                                    <span>{'0'+Math.floor(item.time/60)+':'+(item.time%60)}</span>
                                    <span>{item.singer}</span>
                                    <Icon type="play-circle"  onClick={_this.play.bind(_this,item.id)}/>
                               </div>
                           )
                       }) 
                    }
                </div>
            </div>
        )
    }
    componentWillMount(){
        console.log(this.props)
        var _this=this;
        $.ajax({
           type:'get',
           url:'https://api.bzqll.com/music/netease/topMvList',
           async:false,
           dataType:'json',
           data:{key:579621905},
           success:function(data){
               _this.setState({list:data.data})
           }


        })
        $.ajax({
            type:'get',
            url:'https://api.bzqll.com/music/netease/songList',
            async:false,
            dataType:'json',
            data:{
                key:579621905,
                id:_this.props.location.state.id,
            },
            success:function(data){
                console.log(data.data)
                _this.setState({list2:data.data.songs})
            }
 
 
         })
        
    }
}
export default Tip;